:root{--background: #ffffff;--foreground: #171717}@media(prefers-color-scheme: dark){:root{--background: #0a0a0a;--foreground: #ededed}}html,body{max-width:100vw;height:100%;overflow-x:hidden}body{color:var(--foreground);background:var(--background);font-family:Arial,Helvetica,sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}*{box-sizing:border-box;padding:0;margin:0}a{color:inherit;text-decoration:none}@font-face{font-family:"Montserrat 400";src:local("Montserrat Regular"),url("../../public/fonts/Montserrat-Regular.ttf") format("truetype")}@font-face{font-family:"Montserrat 600";src:local("Montserrat SemiBold"),url("../../public/fonts/Montserrat-SemiBold.ttf") format("truetype")}@font-face{font-family:"Montserrat 700";src:local("Montserrat Bold"),url("../../public/fonts/Montserrat-Bold.ttf") format("truetype")}.container{max-width:1440px;margin:0 auto;padding:0 20px;display:flex;justify-content:center;align-items:center;height:100%;background-color:#093545;background-image:url(../../public/backgrond-bottom.png);background-position:center bottom;background-repeat:no-repeat;background-size:100% 110px}